.container {
  margin: 0 auto;
  max-width: 980px;
  width: 100%;
}

.form {
  //   width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
  & label,
  & input {
    width: 100%;
  }
  & label {
    margin-bottom: 26px;
    display: block;
  }
  & input {
    border: 5px solid #7d41e1;
    border-radius: 10px;
    font-size: 32px;
    font-weight: 500;
    padding: 31px 44px;
    background-color: transparent !important;
  }
}

.label_nameForm {
  border: 5px solid #7d41e1;
  border-radius: 10px;
  padding: 33px 44px;
  display: flex;
  flex-direction: column;
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 75px;

  & input {
    margin-top: 20px;
    border: none;
    font-size: 32px;
    font-weight: 500;
    outline: none;
    padding: 20px 0;
  }
}

.btn {
  font-size: 32px;
  color: white;
  background: linear-gradient(268.25deg, #7d41e1 6.27%, #238ff6 94.13%);
  border-radius: 10px;
  border: none;
  max-width: 436px;
  width: 100%;
  height: 83px;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 100%;
  & label {
    display: block;
    border: 5px solid #7d41e1;
    border-radius: 10px;
    text-align: center;
    padding: 22px 0;
    font-size: 32px;
    cursor: pointer;
    transition: 0.3s;
    &.active {
      background-color: #7d41e1;
      color: white;
    }
    & input {
      appearance: none;
      width: 0;
      height: 0;
      padding: 0;
      border: none;
    }
  }
}

.label_answer {
  display: flex !important;
  align-items: center;
  & input {
    border: none;
    outline: none;
  }
}

.typeInput {
  width: 56px;
  height: 56px;
  border: 5px solid #7d41e1;
  transition: 0.3s;
  &.active {
    border-radius: 50%;
  }
}

.plus {
  width: 56px;
  height: 56px;
  align-self: flex-start;
  margin-bottom: 63px;
  cursor: pointer;
  & img {
    width: 100%;
    height: 100%;
  }
}

.question {
  margin-bottom: 130px;
  width: 100%;
}

.margin-bottom {
  margin-bottom: 63px;
}
