.recent {
  margin-bottom: 100px;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.title {
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 38px;
}

.list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
}

.item {
  border: 5px solid #7d41e1;
  border-radius: 10px;
  cursor: pointer;
  & img {
    margin-bottom: 20px;
    pointer-events: none;
  }
  & p {
    font-size: 28px;
    margin-bottom: 20px;
    text-align: center;
  }
}
