.create-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  margin-bottom: 60px;

  & h1 {
    font-size: 36px;
    margin-bottom: 50px;
  }
  & img {
    margin-bottom: 24px;
    cursor: pointer;
  }
  & p {
    font-size: 32px;
  }
}
